html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  -webkit-animation: bugfix infinite 1s;
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  scrollbar-gutter: auto;
  scroll-behavior: smooth;
}

@media screen and (min-width: 1600px) {
  .disable-scroll {
    top: 0 !important;
  }
}
body {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #3b547f;
  margin: 0;
  padding: 0;
  background: #ebf1ff;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  position: relative;
  overflow-x: hidden;
}
body.ov-hidden {
  overflow: hidden;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  transition: color 0.2s ease;
  text-decoration: none;
}
@media (any-hover: hover) {
  a:hover {
    color: #5f75a7;
  }
}

input,
textarea {
  -webkit-animation: bugfix infinite 1s;
  line-height: inherit;
  margin: 0;
  padding: 0;
}

a {
  color: unset;
}

a,
a:hover {
  text-decoration: none;
}

button,
input,
a,
textarea {
  outline: none;
  cursor: pointer;
  font: inherit;
}
button:focus,
input:focus,
a:focus,
textarea:focus {
  outline: none;
}
button:active,
input:active,
a:active,
textarea:active {
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font: inherit;
  margin: 0;
  padding: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  width: 100%;
  height: 100%;
  display: block;
}

button {
  border: none;
  color: inherit;
  font: inherit;
  text-align: inherit;
  padding: 0;
  background-color: inherit;
}

textarea {
  resize: none;
}

svg {
  width: 100%;
  height: 100%;
  stroke-linejoin: round;
  stroke-linecap: round;
}

ul {
  padding: 0;
  margin: 0;
}

ul li {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  list-style-type: none;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

fieldset {
  padding: 0;
  margin: 0;
  border: 0;
}

.container {
  max-width: 1780px;
  padding: 0 16px;
  margin: 0 auto;
}

.title {
  font-size: 36px;
  font-weight: 700;
  line-height: 50px;
}

main {
  margin: 40px 0;
  flex: 1;
}

.auth-page {
  margin: 68px 0;
}

@media screen and (max-width: 1600px) {
  .container {
    max-width: 1228px;
  }
  .auth-page {
    margin: 40px 0;
  }
  main {
    margin: 15px 0;
  }
}
@media screen and (max-width: 1100px) {
  .title {
    font-size: 32px;
  }
}
@media screen and (max-width: 767px) {
  .title {
    font-size: 20px;
    line-height: 24px;
  }
  main,
  .auth-page {
    margin: 20px 0;
  }
}
.btn {
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 5px;
  padding: 8px 0;
  flex-shrink: 0;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
}
@media screen and (max-width: 767px) {
  .btn {
    font-size: 14px;
    line-height: 18px;
  }
}
.btn img {
  width: 24px;
  height: 24px;
}

.btn-def {
  color: #ffffff;
  background-color: #3b547f;
  transition: all 0.2s ease;
  width: 260px;
  height: 40px;
}
.btn-def svg {
  width: 25px;
  height: 25px;
  fill: #fff;
}
@media (any-hover: hover) {
  .btn-def:hover {
    background-color: #5f75a7;
  }
}

.btn-sec {
  color: #3b547f;
  background-color: transparent;
  width: 280px;
  height: 40px;
  border: 1px solid #3b547f;
  transition: all 0.2s ease;
}
@media (any-hover: hover) {
  .btn-sec:hover {
    background-color: #5f75a7;
    border-color: #5f75a7;
    color: #ffffff;
  }
}

.btn-up {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #5f75a7;
  border-radius: 50%;
  transition: background-color 0.2s ease;
  position: fixed;
  right: 20px;
  bottom: 20px;
  display: none;
}
.btn-up svg {
  fill: #ffffff;
  width: 28px;
  height: 28px;
  transform: rotate(180deg);
}
@media (any-hover: hover) {
  .btn-up:hover {
    background-color: #3b547f;
  }
}

@media screen and (max-width: 767px) {
  .btn {
    border-radius: 4px;
  }
  .btn-def,
  .btn-sec {
    height: 32px;
  }
  .btn-def svg,
  .btn-sec svg {
    width: 18px;
    height: 18px;
  }
  .btn-up {
    display: flex;
  }
}
.input {
  position: relative;
}
.input.--error .input__error {
  transform: translateY(0);
  opacity: 1;
  visibility: visible;
  z-index: 0;
}
.input.--error .input__input {
  border-color: #bf251b;
}
.input.--error .input__btn-notify {
  opacity: 1;
  visibility: visible;
}
.input.--error .input__btn {
  opacity: 0;
  visibility: hidden;
}
.input__label {
  font-size: 14px;
  font-weight: 600;
  line-height: 18px;
  color: #3b547f;
  margin-bottom: 5px;
  display: inline-block;
}
.input__label span {
  color: #bf251b;
}
.input__error {
  font-size: 14px;
  font-weight: 300;
  line-height: 18px;
  color: #bf251b;
  transform: translateY(-100%);
  position: absolute;
  padding-left: 25px;
  opacity: 0;
  visibility: hidden;
  z-index: -2;
  transition: z-index 0.2s ease, opacity 0.2s ease, visibility 0.2s ease, transform 0.2s ease;
}
.input__input {
  background-color: #ebf1ff;
  border-radius: 10px;
  padding: 14px 20px 14px 14px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  border: 1px solid transparent;
  transition: border 0.2s ease;
  color: #3b547f;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  width: 100%;
}
.input__input::placeholder {
  color: #696f79;
}
.input__input:hover {
  border-color: #696f79;
}
.input__input:focus {
  border-color: #3b547f;
}
.input__input:focus ~ .input__btn svg {
  fill: #3b547f;
}
.input__btn-error {
  opacity: 0;
  visibility: hidden;
}
.input__btn, .input__btn-notify {
  width: 25px;
  height: 25px;
  position: absolute;
  right: 20px;
  bottom: calc(50% - 25px);
  transition: opacity 0.2s ease, visibility 0.2s ease;
}
.input__btn svg, .input__btn-notify svg {
  stroke: none;
  transition: fill 0.2s ease;
  stroke-width: 1.5;
  position: absolute;
  top: 0;
}
.input__btn-notify {
  fill: #bf251b;
  opacity: 0;
  visibility: hidden;
}
.input__btn svg:first-child {
  opacity: 0;
  visibility: hidden;
}
.input__btn svg:last-child {
  opacity: 1;
  visibility: visible;
}
.input__btn.--open svg:last-child {
  opacity: 0;
  visibility: hidden;
}
.input__btn.--open svg:first-child {
  opacity: 1;
  visibility: visible;
}
.input__btn svg {
  fill: #696f79;
}
@media (any-hover: hover) {
  .input__btn svg:hover {
    fill: #3b547f;
  }
}
.input--pass .input__input {
  padding-right: 60px;
}

@media screen and (max-width: 767px) {
  .input__input {
    padding: 5px 14px;
    border-radius: 4px;
  }
  .input__btn, .input__btn-notify {
    right: 10px;
    width: 18px;
    height: 18px;
    bottom: calc(50% - 20px);
  }
  .input--pass .input__input {
    padding-right: 36px;
  }
  .input__error {
    font-size: 12px;
    line-height: 14px;
  }
}
.logo {
  width: 150px;
  height: 28px;
}

.tippy-box {
  font-family: "Inter", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  background-color: #3b547f;
  color: #fafbff;
  border-radius: 8px;
}

.tippy-content {
  padding: 12px;
}

.tippy-arrow {
  color: #3b547f;
}

.dropdown {
  position: relative;
  width: 100%;
  cursor: pointer;
}
.dropdown__btn {
  transition: transform 0.2s ease;
  width: 20px;
  height: 20px;
}
.dropdown__btn svg {
  fill: #696f79;
  transition: transform 0.2s ease;
}
.dropdown__headline {
  font-size: 14px;
  font-weight: 600;
  line-height: 18px;
  margin-bottom: 7px;
}
.dropdown__headline span {
  color: #bf251b;
}
.dropdown__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: border-color 0.2s ease;
  background-color: #ffffff;
  border-radius: 8px;
  padding: 14px;
  transition: border 0.2s ease, border-radius 0.2s ease;
  border: 1px solid transparent;
}
@media (any-hover: hover) {
  .dropdown__header:hover {
    border-color: #696f79;
  }
}
.dropdown__content {
  opacity: 0;
  visibility: hidden;
  display: flex;
  flex-direction: column;
  box-shadow: 0px 1px 2px 0px rgba(16, 24, 40, 0.04);
  background: #ffffff;
  transition: all 0.5s ease;
  width: 100%;
  position: absolute;
  z-index: -1;
  padding: 4px 0;
  height: 150px;
  max-height: 0;
  width: 100%;
  border-radius: 0 0 8px 8px;
}
.dropdown__items {
  scrollbar-width: none;
  max-height: 150px;
  overflow-y: auto;
  width: 100%;
}
.dropdown__items::-webkit-scrollbar {
  width: 0;
  height: 0;
}
.dropdown__item {
  cursor: pointer;
  padding: 10px 16px;
}
.dropdown__item input {
  display: none;
}
.dropdown__item input:checked + label {
  color: #3b547f;
}
.dropdown__item label {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  width: 100%;
  overflow: hidden;
  color: #5f75a7;
}
.dropdown__value {
  color: #696f79;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  transition: color 0.2s ease;
}
.dropdown.--active .dropdown__content {
  opacity: 1;
  visibility: visible;
  max-height: 150px;
  z-index: 10;
}
.dropdown.--active .dropdown__header {
  border-radius: 8px 8px 0 0;
}
.dropdown.--active .dropdown__btn svg {
  transform: rotate(180deg);
}
.dropdown.--filled .dropdown__value {
  color: #3b547f;
}

@media screen and (max-width: 767px) {
  .dropdown__header {
    padding: 5px 14px;
    border-radius: 4px;
  }
  .dropdown__content {
    width: calc(100% - 24px);
  }
  .dropdown__item {
    padding: 8px 16px;
  }
}
.drag-area {
  position: relative;
  width: 100%;
  height: 204px;
}
.drag-area__form {
  opacity: 1;
  visibility: visible;
  width: 100%;
  height: 100%;
  position: absolute;
  border-radius: 8px;
  border: 1.5px dashed #696f79;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 45px;
  transition: border-color 0.2s ease, background 0.2s ease, opacity 0.2s ease, visibility 0.2s ease;
}
.drag-area__form.--active {
  border-color: #3b547f;
  background: #fafbff;
}
.drag-area__form.--error {
  border-color: #bf251b;
}
.drag-area__form.--error .drag-area__sm {
  color: #bf251b;
}
.drag-area.--files .drag-area__form {
  opacity: 0;
  visibility: hidden;
}
.drag-area.--files .drag-area__files {
  opacity: 1;
  visibility: visible;
}
.drag-area__files {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  border-radius: 8px;
}
.drag-area__file {
  padding: 12px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #ebf1ff;
}
.drag-area__filename {
  color: #3b547f;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}
.drag-area__fileclose {
  width: 24px;
  height: 24px;
}
.drag-area__fileclose svg {
  stroke: #696f79;
  fill: none;
  transition: stroke 0.2s ease;
}
@media (any-hover: hover) {
  .drag-area__fileclose:hover svg {
    stroke: #3b547f;
  }
}
.drag-area__btn {
  margin: 0 auto 5px;
}
.drag-area__up {
  text-transform: uppercase;
  margin-bottom: 10px;
}
.drag-area__sm, .drag-area__up {
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  color: #696f79;
  text-align: center;
}
@media screen and (max-width: 767px) {
  .drag-area__sm, .drag-area__up {
    font-size: 14px;
    line-height: 18px;
  }
}

@media screen and (max-width: 767px) {
  .drag-area {
    height: 164px;
  }
  .drag-area__form {
    padding: 20px 12px;
    border-radius: 0;
    border-left: none;
    border-right: none;
  }
  .drag-area__files {
    border-radius: 0;
  }
  .drag-area__file {
    padding: 12px;
  }
  .drag-area__btn {
    width: 100%;
  }
  .drag-area__sm, .drag-area__up {
    text-wrap: balance;
  }
}
.more-btns {
  position: relative;
}
.more-btns__btn {
  width: 20px;
  height: 20px;
}
.more-btns__btn svg {
  stroke: none;
  fill: #3b547f;
}
.more-btns__content {
  position: absolute;
  width: 157px;
  right: 8px;
  padding: 15px 10px;
  border-radius: 8px;
  border: 1px solid #3b547f;
  display: flex;
  flex-direction: column;
  row-gap: 8px;
  opacity: 0;
  visibility: hidden;
  background-color: #fff;
  z-index: -2;
  transition: opacity 0.2s ease, visibility 0.2s ease, z-index 0.2s ease;
}
.more-btns__content.--active {
  opacity: 1;
  visibility: visible;
  z-index: 2;
}
.more-btns__link {
  display: flex;
  align-items: center;
  column-gap: 5px;
  display: flex;
  align-items: center;
  column-gap: 5px;
}
.more-btns__link span {
  border-bottom: 1px solid transparent;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  transition: border-color 0.2s ease;
}
@media (any-hover: hover) {
  .more-btns__link:not(:disabled):hover svg:first-child {
    opacity: 0;
    visibility: hidden;
  }
  .more-btns__link:not(:disabled):hover svg:last-child {
    opacity: 1;
    visibility: visible;
  }
  .more-btns__link:not(:disabled):hover span {
    color: #3b547f;
    border-color: #3b547f;
  }
}
.more-btns__link:disabled {
  cursor: default;
}
.more-btns__link:disabled span {
  color: #b0b0b0;
}
.more-btns__link:disabled svg:first-child,
.more-btns__link:disabled svg:last-child {
  stroke: #b0b0b0;
}
.more-btns__link-icon {
  width: 20px;
  height: 20px;
  position: relative;
  flex-shrink: 0;
}
.more-btns__link-icon svg {
  position: absolute;
  transition: opacity 0.2s ease, visibility 0.2s ease;
}
.more-btns__link-icon svg:first-child {
  fill: none;
  stroke: #3b547f;
  opacity: 1;
  visibility: visible;
}
.more-btns__link-icon svg:last-child {
  fill: #3b547f;
  stroke: none;
  opacity: 0;
  visibility: hidden;
}

.header {
  background: #3b547f;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
  height: 70px;
}
.header__menus {
  position: relative;
}
.header__btn {
  display: flex;
  align-items: center;
  column-gap: 5px;
  cursor: pointer;
}
.header__btn.--active .header__btn-arrow {
  transform: rotate(180deg);
}
.header__btn.--active .header__btn-arrow svg {
  background-color: #ffffff;
  fill: #3b547f;
}
.header__btn-icon {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
}
.header__btn-icon svg {
  width: 25px;
  height: 25px;
  stroke: #3b547f;
  fill: none;
  stroke-width: 1.5;
}
.header__btn-arrow {
  transition: transform 0.2s ease;
  width: 25px;
  height: 25px;
}
.header__btn-arrow svg {
  fill: #ffffff;
  stroke: none;
  background: none;
  border-radius: 50%;
  transition: fill 0.2s ease, background 0.2s ease;
}
.header__menu {
  border: 1px solid #3b547f;
  padding: 15px 15px 15px 10px;
  border-radius: 8px;
  background-color: #fafbff;
  display: flex;
  flex-direction: column;
  row-gap: 8px;
  position: absolute;
  top: 42px;
  right: 0;
  width: 183px;
  opacity: 0;
  visibility: hidden;
  z-index: -2;
}
.header__menu.--active {
  opacity: 1;
  visibility: visible;
  z-index: 2;
}
.header__menu svg {
  fill: none;
  stroke: #3b547f;
}
.header__menu-item {
  display: flex;
  align-items: center;
  column-gap: 5px;
}
.header__menu-item span {
  border-bottom: 1px solid transparent;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  transition: border-color 0.2s ease;
}
@media (any-hover: hover) {
  .header__menu-item:hover svg:first-child {
    opacity: 0;
    visibility: hidden;
  }
  .header__menu-item:hover svg:last-child {
    opacity: 1;
    visibility: visible;
  }
  .header__menu-item:hover span {
    color: #3b547f;
    border-color: #3b547f;
  }
}
.header__menu-icon {
  position: relative;
  width: 20px;
  height: 20px;
}
.header__menu-icon svg {
  transition: opacity 0.2s ease, visibility 0.2s ease;
  position: absolute;
}
.header__menu-icon svg:first-child {
  fill: none;
  stroke: #3b547f;
  opacity: 1;
  visibility: visible;
}
.header__menu-icon svg:last-child {
  fill: #3b547f;
  stroke: none;
  opacity: 0;
  visibility: hidden;
}

.footer {
  background: #3b547f;
  height: 72px;
}

.search {
  position: relative;
  width: 544px;
}
.search__input {
  background-color: #fafbff;
  border-radius: 10px;
  padding: 10px 42px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  border: 1px solid transparent;
  transition: border-color 0.2s ease;
  color: #3b547f;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  width: 100%;
}
.search__input::placeholder {
  color: #b0b0b0;
}
.search__input:focus {
  border-color: #3b547f;
}
.search__input:focus ~ .search__btn svg {
  stroke: #3b547f;
}
.search__btn-submit {
  left: 15px;
}
.search__btn-reset {
  right: 15px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.2s ease, visibility 0.2s ease;
}
.search__btn-reset.--active {
  opacity: 1;
  visibility: visible;
}
.search__btn-reset svg {
  stroke-width: 2;
}
.search__btn-reset, .search__btn-submit {
  width: 24px;
  height: 24px;
  position: absolute;
  top: calc(50% - 12px);
}
@media (any-hover: hover) {
  .search__btn-reset:hover svg, .search__btn-submit:hover svg {
    stroke: #3b547f;
  }
}
.search__btn-reset svg, .search__btn-submit svg {
  stroke-width: 1.5;
  stroke: #b0b0b0;
  fill: none;
  transition: stroke 0.2s ease;
}

@media screen and (max-width: 1600px) {
  .search {
    width: 440px;
  }
}
@media screen and (max-width: 1100px) {
  .search {
    width: 100%;
  }
}
@media screen and (max-width: 767px) {
  .search__btn-reset, .search__btn-submit {
    width: 18px;
    height: 18px;
    top: calc(50% - 9px);
  }
  .search__btn-submit {
    left: 10px;
  }
  .search__btn-reset {
    right: 10px;
  }
  .search__input {
    border-radius: 4px;
    padding: 5px 32px;
  }
}
.pagination {
  display: flex;
  align-items: center;
  column-gap: 10px;
}
.pagination__btn {
  width: 24px;
  height: 24px;
  position: relative;
}
@media (any-hover: hover) {
  .pagination__btn:hover svg path:first-child {
    fill: #3b547f;
    stroke: #3b547f;
  }
  .pagination__btn:hover svg path:last-child {
    stroke: #ffffff;
  }
}
.pagination__btn:active svg path:first-child {
  fill: #3b547f;
  stroke: #3b547f;
}
.pagination__btn:active svg path:last-child {
  stroke: #ffffff;
}
.pagination__btn svg {
  fill: none;
  stroke: #696f79;
  position: absolute;
  top: 0;
  left: 0;
}
.pagination__btn svg path {
  transition: fill 0.2s ease, stroke 0.2s ease;
}
.pagination__info {
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  color: #696f79;
}
@media screen and (max-width: 767px) {
  .pagination__info {
    font-size: 14px;
    line-height: 18px;
  }
}

@media screen and (max-width: 767px) {
  .pagination__btn {
    width: 20px;
    height: 20px;
  }
}
.graph-modal {
  background: rgba(0, 0, 0, 0.5019607843);
  display: flex;
  align-items: center;
  justify-content: center;
}
.graph-modal::before {
  display: none;
}
.graph-modal__close {
  background: none;
  top: 20px;
  right: 20px;
  width: 24px;
  height: 24px;
}
.graph-modal__close svg {
  width: 100%;
  height: 100%;
  stroke: #696f79;
  stroke-width: 2;
  transition: stroke 0.2s ease;
}
@media (any-hover: hover) {
  .graph-modal__close:hover svg {
    stroke: #3b547f;
  }
}
.graph-modal__container {
  border-radius: 8px;
  background-color: #ebf1ff;
}
.graph-modal .h2 {
  margin: 0;
}

.modal-doc {
